<template>
  <div
    class="root"
    :class="{ hidden: isOpened }"
  >
  <div class="main-page">
    <header-block @toggleMenu="onMenuToggle"/>
    <div class="section-wrapper">
      <header-slider/>
      <div class="content-wrapper" v-if="!showModal">
        <div class="content-inner-about">
        <h1 class="content-title">О компании</h1>
        <div class="content-article">
          <p class="content-article-p">
          Что-то более продвинутое делается с помощью покадровой анимации — когда для разных состояний объекта (стоит, идет, в прыжке,
          атакует) создается несколько спрайтов (двумерное изображение), которые сменяют друг друга с определенным интервалом.
          </p>
          <a class="content-loadmore content-loadmore--article" href="#">
            Подробнее
          </a>
        </div>
        </div>
      </div>
      <div class="content-wrapper" v-if="!showModal">
        <div class="news-header">
          <h1 class="content-title">Новости</h1>
          <span class="content-subtitle">Все новости</span>
        </div>
        <div class="content-news">
          <div class="news-img"></div>
          <div class="news-img"></div>
          <div class="news-img"></div>
        </div>
        <a class="content-loadmore content-loadmore--news" v-show="!readMoreNews" @click.prevent="activateReadMoreNews" href="#">
          Показать еще
        </a>
        <div class="content-news" v-if="readMoreNews">
          <div class="news-img"></div>
          <div class="news-img"></div>
          <div class="news-img"></div>
        </div>
      </div>
    </div>
    <RegistrationForm
      class="content-wrapper registration-form"
      @register="onLogin"
      v-if="!showModal">
    </RegistrationForm>
    <Footer
      v-if="!showModal"
    />
  </div>
  <div
    class="modal-overlay"
    v-if="showModal"
  >
    <div class="modal-overlay-inner">
      <div class="modal-overlay-content">
        <div class="modal-overlay-img"></div>
        <div class="modal-overlay-text">Благодарим за подписку {{userName}}</div>
      </div>
    </div>
  </div>
  </div>
</template>

<script>
import HeaderBlock from '@/components/Header'
import HeaderSlider from '@/components/header-slider'
import Footer from '@/components/Footer'
import RegistrationForm from '@/components/RegistrationForm'

export default {
  name: 'App',
  components: {
    Footer,
    HeaderBlock,
    HeaderSlider,
    RegistrationForm
  },
  data () {
    return {
      showModal: false,
      menuOpen: false,
      windowWidth: 0,
      readMoreNews: false,
      userName: null
    }
  },
  computed: {
    isOpened () {
      return this.isMobile && this.menuOpen
    },
    isMobile () {
      return this.windowWidth <= 760
    }
  },
  created () {
    window.addEventListener('resize', this.handleResize)
    this.handleResize()
  },
  destroyed () {
    window.removeEventListener('resize', this.handleResize)
  },
  methods: {
    onLogin (data) {
      console.log('child component said login', data)
      this.showModal = true
      this.userName = data.name
    },
    onMenuToggle (data) {
      this.menuOpen = data.isOpened
    },
    handleResize () {
      this.windowWidth = window.innerWidth
    },
    activateReadMoreNews () {
      this.readMoreNews = true
    }
  }
}
</script>

