<template>
  <form
    id="registration-form"
    @submit="checkForm"
  >
    <div class="registration-form-inner">
      <h2 class="registration-form-h2">Подпишитесь на рассылку</h2>
    <div class="registration-form-field-wrap">
    <p class="registration-form-field">
      <span class="registration-form-field-error">{{this.errors.name}}</span>
      <label for="name" class="registration-form-label">Имя</label>
      <input
        id="name"
        class="registration-form-input"
        v-model="name"
        type="text"
        name="name"
      >
    </p>
    <p class="registration-form-field">
      <span class="registration-form-field-error">{{this.errors.email}}</span>
      <label for="email" class="registration-form-label">Email</label>
      <input
        id="email"
        class="registration-form-input"
        v-model="email"
        type="email"
        name="email"
        placeholder="example@example.com"
      >
    </p>
    </div>
    <p class="registration-form-field">
      <span class="registration-form-field-error">{{this.errors.comment}}</span>
      <label for="comment" class="registration-form-label registration-form-label--comment">Введите коментарий</label>
      <textarea
        id="comment"
        class="registration-form-comment"
        v-model="comment"
        type="email"
        name="email"
      />
    </p>
    <div class="submit-wrap">
      <button @click='checkForm' class="registration-form-submit">Зарегистрироваться</button>
    </div>
    </div>
  </form>
</template>

<script>
export default {
  name: 'RegistrationForm',
  data () {
    return {
      errors: {
        name: null,
        email: null,
        comment: null
      },
      name: null,
      email: null,
      comment: null
    }
  },
  methods: {
    checkForm: function (e) {
      //! ! todo - переделать через обьект и конкретные свойства
      this.errors = {
        name: null,
        email: null,
        comment: null
      }
      if (!this.name) this.errors.name = 'Введите Имя'
      else if (!this.validName(this.name)) {
        this.errors.name = 'Поле не должно содержать цифры'
      }
      if (!this.comment) this.errors.comment = 'Заполните поле комментария'
      if (!this.email) {
        this.errors.email = 'Введите Email'
      } else if (!this.validEmail(this.email)) {
        this.errors.email = 'Введите валидный email адресс'
      }
      console.log(Object.values(this.errors))
      console.log(Object.values(this.errors).every(err => err === null))
      if (Object.values(this.errors).every(err => err === null)) {
        this.$emit('register', {
          email: this.email,
          name: this.name
        })
      }
      console.log(this.errors)
      e.preventDefault()
    },
    validEmail: function (email) {
      // eslint-disable-next-line no-useless-escape
      const re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
      return re.test(email)
    },
    validName: function (name) {
      const nameReg = /^([^0-9]*)$/
      return nameReg.test(name)
    }
  }
}
</script>

